import { getCurrentUser } from '../data/usersMockData';
import { TaskWithUsers } from '../services/taskService';

/**
 * Utility functions for task permissions
 * Ch ngi to cng vic mi c quyn chnh sa, xa
 */

// Cache  lu kt qu kim tra quyn (ti u hiu sut)
const permissionCache = new Map<string, { permissions: any; timestamp: number }>();
const CACHE_DURATION = 5 * 60 * 1000; // 5 pht

// Ghi log khi c ngi c gng truy cp tri php
function logUnauthorizedAccess(action: string, taskId: string, userId: string, taskName: string) {
  const logEntry = {
    timestamp: new Date().toISOString(),
    action: action, // hnh ng: "chnh sa", "xa", v.v.
    taskId: taskId,
    taskName: taskName,
    userId: userId,
    type: 'UNAUTHORIZED_ACCESS', // loi log
  };

  console.warn(' Truy cp tri php:', logEntry);

  // C th gi ln server  lu vo database
  // await sendAuditLog(logEntry);
}

/**
 * Kim tra xem user c quyn da trn vai tr (role-based)
 * @param userRole - Vai tr ca user: 'admin', 'manager', 'employee'
 * @returns true nu user c quyn qun l tt c tasks
 */
function hasAdminPermissions(userRole?: string): boolean {
  // Admin v Manager c th chnh sa tt c cng vic
  return userRole === 'admin' || userRole === 'manager' || userRole === 'retail_director';
}

/**
 * Kim tra xem user hin ti c phi l ch s hu ca task khng
 * @param task - Task cn kim tra
 * @param currentUserId - ID ca user hin ti (optional, s ly t getCurrentUser nu khng c)
 * @returns true nu user hin ti l ch s hu task
 */
export function isTaskOwner(task: TaskWithUsers, currentUserId?: string): boolean {
  try {
    // Ly current user nu khng c truyn vo
    const currentUser = getCurrentUser();
    const userId = currentUserId || currentUser?.id;

    if (!userId || !task) {
      console.warn(' isTaskOwner: Missing userId or task');
      return false;
    }

    // Kim tra quyn admin/manager trc (h c th chnh sa tt c)
    if (hasAdminPermissions(currentUser?.role)) {
      console.log(' Admin/Manager c quyn chnh sa tt c tasks');
      return true;
    }

    // Kim tra xem user hin ti c phi l ngi to task khng
    const isOwner = task.createdBy?.id === userId;

    console.log(' Task permission check:', {
      taskId: task.id,
      taskName: task.name,
      createdBy: task.createdBy?.id,
      currentUser: userId,
      userRole: currentUser?.role,
      isOwner: isOwner,
    });

    return isOwner;
  } catch (error) {
    console.error(' Error checking task ownership:', error);
    return false;
  }
}

/**
 * Kim tra xem user hin ti c quyn chnh sa task khng
 * @param task - Task cn kim tra
 * @param currentUserId - ID ca user hin ti (optional)
 * @returns true nu user c quyn chnh sa
 */
export function canEditTask(task: TaskWithUsers, currentUserId?: string): boolean {
  // Hin ti ch ch s hu mi c quyn chnh sa
  return isTaskOwner(task, currentUserId);
}

/**
 * Kim tra xem user hin ti c quyn xa task khng
 * @param task - Task cn kim tra
 * @param currentUserId - ID ca user hin ti (optional)
 * @returns true nu user c quyn xa
 */
export function canDeleteTask(task: TaskWithUsers, currentUserId?: string): boolean {
  // Hin ti ch ch s hu mi c quyn xa
  return isTaskOwner(task, currentUserId);
}

/**
 * Kim tra xem user hin ti c quyn thay i trng thi task khng
 * @param task - Task cn kim tra
 * @param currentUserId - ID ca user hin ti (optional)
 * @returns true nu user c quyn thay i trng thi
 */
export function canChangeTaskStatus(task: TaskWithUsers, currentUserId?: string): boolean {
  // Hin ti ch ch s hu mi c quyn thay i trng thi
  return isTaskOwner(task, currentUserId);
}

/**
 * Kim tra xem user hin ti c quyn thay i u tin task khng
 * @param task - Task cn kim tra
 * @param currentUserId - ID ca user hin ti (optional)
 * @returns true nu user c quyn thay i u tin
 */
export function canChangePriority(task: TaskWithUsers, currentUserId?: string): boolean {
  // Hin ti ch ch s hu mi c quyn thay i u tin
  return isTaskOwner(task, currentUserId);
}

/**
 * Kim tra xem user hin ti c quyn thay i danh mc cng vic khng
 * @param task - Task cn kim tra
 * @param currentUserId - ID ca user hin ti (optional)
 * @returns true nu user c quyn thay i danh mc
 */
export function canChangeWorkType(task: TaskWithUsers, currentUserId?: string): boolean {
  // Hin ti ch ch s hu mi c quyn thay i danh mc
  return isTaskOwner(task, currentUserId);
}

/**
 * Kim tra xem user hin ti c quyn assign task cho ngi khc khng
 * @param task - Task cn kim tra
 * @param currentUserId - ID ca user hin ti (optional)
 * @returns true nu user c quyn assign
 */
export function canAssignTask(task: TaskWithUsers, currentUserId?: string): boolean {
  // Hin ti ch ch s hu mi c quyn assign
  return isTaskOwner(task, currentUserId);
}

/**
 * Ly thng tin quyn hn ca user i vi mt task (c cache  ti u)
 * @param task - Task cn kim tra
 * @param currentUserId - ID ca user hin ti (optional)
 * @returns Object cha tt c quyn hn
 */
export function getTaskPermissions(task: TaskWithUsers, currentUserId?: string) {
  const userId = currentUserId || getCurrentUser()?.id;
  const cacheKey = `${task.id}-${userId}`;

  // Kim tra cache trc ( tng tc )
  const cached = permissionCache.get(cacheKey);
  if (cached && Date.now() - cached.timestamp < CACHE_DURATION) {
    console.log(' S dng cache cho permissions ca task:', task.name);
    return cached.permissions;
  }

  const isOwner = isTaskOwner(task, currentUserId);

  const permissions = {
    isOwner,
    canEdit: canEditTask(task, currentUserId),
    canDelete: canDeleteTask(task, currentUserId),
    canChangeStatus: canChangeTaskStatus(task, currentUserId),
    canChangePriority: canChangePriority(task, currentUserId),
    canChangeWorkType: canChangeWorkType(task, currentUserId),
    canAssign: canAssignTask(task, currentUserId),
    // Quyn xem lun c (v  thy task trong list)
    canView: true,
  };

  // Lu vo cache
  permissionCache.set(cacheKey, {
    permissions,
    timestamp: Date.now(),
  });

  return permissions;
}

/**
 * Xa cache permissions khi c thay i (real-time updates)
 * @param taskId - ID ca task b thay i
 */
export function clearPermissionCache(taskId?: string) {
  if (taskId) {
    // Xa cache cho task c th
    for (const key of permissionCache.keys()) {
      if (key.startsWith(taskId)) {
        permissionCache.delete(key);
      }
    }
    console.log('  xa cache permissions cho task:', taskId);
  } else {
    // Xa ton b cache
    permissionCache.clear();
    console.log('  xa ton b cache permissions');
  }
}

/**
 * To thng bo li khi user khng c quyn (v ghi log)
 * @param action - Hnh ng user ang c gng thc hin
 * @param taskName - Tn ca task
 * @param taskId - ID ca task ( ghi log)
 * @returns Thng bo li
 */
export function getPermissionErrorMessage(
  action: string,
  taskName: string,
  taskId?: string
): string {
  const currentUser = getCurrentUser();

  // Ghi log truy cp tri php
  if (taskId && currentUser?.id) {
    logUnauthorizedAccess(action, taskId, currentUser.id, taskName);
  }

  return ` Bn khng c quyn ${action} cng vic "${taskName}". Ch ngi to cng vic hoc Admin/Manager mi c quyn thc hin hnh ng ny.`;
}

/**
 * Kim tra v thng bo li nu khng c quyn
 * @param task - Task cn kim tra
 * @param action - Hnh ng cn thc hin
 * @param requiredPermission - Quyn cn thit (v d: 'canEdit', 'canDelete')
 * @returns true nu c quyn, false v hin thng bo nu khng c quyn
 */
export function checkPermissionWithAlert(
  task: TaskWithUsers,
  action: string,
  requiredPermission: keyof ReturnType<typeof getTaskPermissions>
): boolean {
  const permissions = getTaskPermissions(task);

  if (!permissions[requiredPermission]) {
    alert(getPermissionErrorMessage(action, task.name, task.id));
    return false;
  }

  return true;
}
