import React from 'react';

export type TaskStatus = 'new-requests' | 'approved' | 'live';

interface StatusBadgeProps {
  value: TaskStatus;
  onChange?: (value: TaskStatus) => void;
  className?: string;
}

const statusMap: Record<TaskStatus, { label: string; color: string }> = {
  'new-requests': {
    label: 'Cha tin hnh',
    color: 'bg-yellow-500/20 text-yellow-300 border-yellow-500/30',
  },
  approved: {
    label: 'ang tin hnh',
    color: 'bg-blue-500/20 text-blue-300 border-blue-500/30',
  },
  live: {
    label: ' hon thnh',
    color: 'bg-green-500/20 text-green-300 border-green-500/30',
  },
};

const StatusBadge: React.FC<StatusBadgeProps> = ({ value, onChange, className = '' }) => {
  const info = statusMap[value] || statusMap['new-requests'];

  return (
    <div className={`relative inline-flex items-center ${className}`}>
      <div
        className={`px-2 py-1 rounded-full text-xs font-medium border ${info.color} bg-gray-800/50`}
        title={info.label}
      >
        {info.label}
      </div>
      {onChange && (
        <select
          aria-label="i trng thi"
          value={value}
          onChange={(e) => onChange(e.target.value as TaskStatus)}
          className="absolute inset-0 opacity-0 cursor-pointer"
        >
          <option value="new-requests">Cha tin hnh</option>
          <option value="approved">ang tin hnh</option>
          <option value="live"> hon thnh</option>
        </select>
      )}
    </div>
  );
};

export default StatusBadge;

