import React from 'react';
import { ChevronDown } from 'lucide-react';

interface StatusPriorityEditorProps {
  status: 'new-requests' | 'approved' | 'live';
  priority: 'low' | 'normal' | 'high';
  onStatusChange: (status: 'new-requests' | 'approved' | 'live') => void;
  onPriorityChange: (priority: 'low' | 'normal' | 'high') => void;
  isEditMode: boolean;
}

const StatusPriorityEditor: React.FC<StatusPriorityEditorProps> = ({
  status,
  priority,
  onStatusChange,
  onPriorityChange,
  isEditMode
}) => {
  const getStatusInfo = (status: string) => {
    switch (status) {
      case 'new-requests':
        return { label: 'Yu cu mi', color: 'bg-yellow-500/20 text-yellow-300 border-yellow-500/30' };
      case 'approved':
        return { label: ' duyt', color: 'bg-blue-500/20 text-blue-300 border-blue-500/30' };
      case 'live':
        return { label: 'ang thc hin', color: 'bg-green-500/20 text-green-300 border-green-500/30' };
      default:
        return { label: 'Khng xc nh', color: 'bg-gray-500/20 text-gray-300 border-gray-500/30' };
    }
  };

  const getPriorityInfo = (priority: string) => {
    switch (priority) {
      case 'low':
        return { label: 'Thp', color: 'bg-gray-500/20 text-gray-300 border-gray-500/30' };
      case 'normal':
        return { label: 'Bnh thng', color: 'bg-blue-500/20 text-blue-300 border-blue-500/30' };
      case 'high':
        return { label: 'Cao', color: 'bg-red-500/20 text-red-300 border-red-500/30' };
      default:
        return { label: 'Bnh thng', color: 'bg-blue-500/20 text-blue-300 border-blue-500/30' };
    }
  };

  const statusInfo = getStatusInfo(status);
  const priorityInfo = getPriorityInfo(priority);

  if (!isEditMode) {
    return (
      <div className="flex items-center gap-2">
        <span className={`px-2 py-1 rounded-full text-xs font-medium border ${statusInfo.color}`}>
          {statusInfo.label}
        </span>
        <span className={`px-2 py-1 rounded-full text-xs font-medium border ${priorityInfo.color}`}>
          {priorityInfo.label}
        </span>
      </div>
    );
  }

  return (
    <div className="flex items-center gap-2">
      {/* Status Dropdown */}
      <div className="relative">
        <div className={`relative px-3 py-1 rounded-full text-xs font-medium border cursor-pointer ${statusInfo.color} bg-gray-800/50`}>
          <select
            value={status}
            onChange={(e) => onStatusChange(e.target.value as 'new-requests' | 'approved' | 'live')}
            className="absolute inset-0 w-full h-full opacity-0 cursor-pointer"
          >
            <option value="new-requests">Yu cu mi</option>
            <option value="approved"> duyt</option>
            <option value="live">ang thc hin</option>
          </select>
          <div className="flex items-center justify-between">
            <span>{statusInfo.label}</span>
            <ChevronDown className="w-3 h-3 ml-1" />
          </div>
        </div>
      </div>

      {/* Priority Dropdown */}
      <div className="relative">
        <div className={`relative px-3 py-1 rounded-full text-xs font-medium border cursor-pointer ${priorityInfo.color} bg-gray-800/50`}>
          <select
            value={priority}
            onChange={(e) => onPriorityChange(e.target.value as 'low' | 'normal' | 'high')}
            className="absolute inset-0 w-full h-full opacity-0 cursor-pointer"
          >
            <option value="low">Thp</option>
            <option value="normal">Bnh thng</option>
            <option value="high">Cao</option>
          </select>
          <div className="flex items-center justify-between">
            <span>{priorityInfo.label}</span>
            <ChevronDown className="w-3 h-3 ml-1" />
          </div>
        </div>
      </div>
    </div>
  );
};

export default StatusPriorityEditor;
