import React from 'react';

export type WorkType =
  | 'other'
  | 'sbg-new'
  | 'sbg-old'
  | 'partner-new'
  | 'partner-old'
  | 'kts-new'
  | 'kts-old'
  | 'customer-new'
  | 'customer-old';

interface WorkTypeBadgeProps {
  value: WorkType;
  onChange?: (value: WorkType) => void;
  className?: string;
}

const workTypeMap: Record<WorkType, { label: string; color: string }> = {
  'sbg-new': {
    label: 'SBG mi',
    color: 'bg-blue-500/20 text-blue-300 border-blue-500/30',
  },
  'sbg-old': {
    label: 'SBG c',
    color: 'bg-blue-600/20 text-blue-400 border-blue-600/30',
  },
  'partner-new': {
    label: 'i tc mi',
    color: 'bg-green-500/20 text-green-300 border-green-500/30',
  },
  'partner-old': {
    label: 'i tc c',
    color: 'bg-green-600/20 text-green-400 border-green-600/30',
  },
  'kts-new': {
    label: 'KTS mi',
    color: 'bg-purple-500/20 text-purple-300 border-purple-500/30',
  },
  'kts-old': {
    label: 'KTS c',
    color: 'bg-purple-600/20 text-purple-400 border-purple-600/30',
  },
  'customer-new': {
    label: 'Khch hng mi',
    color: 'bg-orange-500/20 text-orange-300 border-orange-500/30',
  },
  'customer-old': {
    label: 'Khch hng c',
    color: 'bg-orange-600/20 text-orange-400 border-orange-600/30',
  },
  other: {
    label: 'Cng vic khc',
    color: 'bg-gray-500/20 text-gray-300 border-gray-500/30',
  },
};

const WorkTypeBadge: React.FC<WorkTypeBadgeProps> = ({ value, onChange, className = '' }) => {
  const info = workTypeMap[value] || workTypeMap['other'];

  return (
    <div className={`relative inline-flex items-center ${className}`}>
      <div
        data-testid="work-type-badge"
        className={`px-2 py-1 rounded-full text-xs font-medium border ${info.color} bg-gray-800/50`}
        title={info.label}
      >
        {info.label}
      </div>
      {onChange && (
        <select
          aria-label="i danh mc cng vic"
          value={value}
          onChange={e => onChange(e.target.value as WorkType)}
          className="absolute inset-0 opacity-0 cursor-pointer"
        >
          <option value="sbg-new">SBG mi</option>
          <option value="sbg-old">SBG c</option>
          <option value="partner-new">i tc mi</option>
          <option value="partner-old">i tc c</option>
          <option value="kts-new">KTS mi</option>
          <option value="kts-old">KTS c</option>
          <option value="customer-new">Khch hng mi</option>
          <option value="customer-old">Khch hng c</option>
          <option value="other">Cng vic khc</option>
        </select>
      )}
    </div>
  );
};

export default WorkTypeBadge;
